<template>
  <v-container class="search-page" fluid>
    <v-row justify="center" class="mb-6">
      <v-col cols="12">
        <h2 class="text-center">{{ titlePage }}</h2>
        <v-divider class="my-4" />
      </v-col>
    </v-row>

    <v-row justify="center">
      <v-col cols="12" class="text-center" v-if="loading">
        <v-progress-circular indeterminate color="primary" size="50" />
      </v-col>

      <v-col cols="12" v-else>
        <v-alert v-if="movies.length === 0" class="text-center">
          Không tìm thấy phim nào.
          <br />
          <router-link to="/home">
            <v-btn variant="outlined" class="mt-2">Về trang chủ</v-btn>
          </router-link>
        </v-alert>

        <router-link
          v-for="movie in movies"
          :key="movie.id"
          :to="{ name: 'MovieDetail', params: { slug: movie.slug } }"
          class="text-decoration-none"
        >
          <v-card class="mb-5 overflow-hidden movie-card" elevation="4" hover>
            <v-row>
              <v-col cols="12" md="4">
                <v-img
                  :src="getOptimizedImage(movie.poster_url)"
                  :lazy-src="getOptimizedImage(movie.poster_url)"
                  :alt="movie.name"
                  aspect-ratio="16/9"
                  class="movie-image"
                  transition="fade-transition"
                  cover
                >
                  <template #default>
                    <v-chip
                      size="small"
                      class="badge-top-left bg-red text-white"
                      label
                    >
                      {{
                        movie.episode_current === 'Tập 0'
                          ? 'Full-' + movie.lang
                          : movie.episode_current + '-' + movie.lang
                      }}
                    </v-chip>
                  </template>
                </v-img>
              </v-col>
              <v-col cols="12" md="8" class="pa-6">
                <h3 class="text-left title">{{ movie.name }}</h3>
                <div class="genre-section mb-3">
                  <v-chip
                    v-for="(genre, index) in movie.category"
                    :key="index"
                    class="ma-1"
                    label
                  >
                    {{ genre.name }}
                  </v-chip>
                </div>

                <div class="meta-info mb-2 d-flex align-center flex-wrap">
                  <v-rating
                    :value="movie.tmdb ? movie.tmdb.vote_average / 2 : 0"
                    active-color="orange"
                    color="orange-lighten-1"
                    half-increments
                    size="small"
                    density="compact"
                    readonly
                  ></v-rating>
                  <v-icon size="18" class="me-1 text-grey ms-3">mdi-calendar</v-icon>
                  <span class="me-4">{{ movie.year }}</span>
                </div>

                <p class="text-body-2 description-text">
                  Miêu tả: {{ movie.origin_name }}
                </p>

                <div class="action-buttons mt-4">
                  <v-btn
                    variant="flat"
                    color="primary"
                    class="me-2"
                    prepend-icon="mdi-play-circle"
                  >
                    Xem ngay
                  </v-btn>
                  <v-btn
                    color="secondary"
                    variant="outlined"
                    prepend-icon="mdi-share-variant"
                  >
                    Chia sẻ
                  </v-btn>
                </div>
              </v-col>
            </v-row>
          </v-card>
        </router-link>

        <v-pagination
          :model-value="currentPage"
          :length="Math.ceil(totalMovies / moviesPerPage)"
          class="my-4 justify-center"
          color="warning"
          @update:modelValue="handlePageChange"
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { urlImage, CategorisDetail } from '@/model/api'

export default {
  name: 'TheLoai',
  props: ['path'],
  data() {
    return {
      loading: true,
      currentPage: 1,
      moviesPerPage: 0,
      totalMovies: 0,
      movies: [],
      urlImage,
      titlePage: '',
      lastPath: ''
    }
  },
  methods: {
    ListMovie(pathParam) {
      this.loading = true
      const pathToUse = pathParam || this.path
      CategorisDetail(`${pathToUse}?page=${this.currentPage}`, (result) => {
        if (result.status === 'success') {
          this.movies = result.data.items || []
          this.titlePage = result.data.titlePage || ''
          this.totalMovies = result.data.params?.pagination?.totalItems || 0
          this.moviesPerPage = result.data.params?.pagination?.totalItemsPerPage || 1
          if (result.data.seoOnPage) this.updateMetaTags(result.data.seoOnPage)
        }
        this.loading = false
      }, (err) => {
        console.error(err)
        this.loading = false
      })
    },
    getOptimizedImage(imagePath) {
      return `${this.urlImage + encodeURIComponent(imagePath)}&w=384&q=100`
    },
    updateMetaTags(seo) {
      document.title = seo.titleHead || 'Phim hay'
      const removeOldMeta = (key, attr = 'name') => {
        const old = document.querySelectorAll(`meta[${attr}="${key}"]`)
        old.forEach(tag => tag.remove())
      }
      const setMeta = (key, content, attr = 'name') => {
        if (!content) return
        const meta = document.createElement('meta')
        meta.setAttribute(attr, key)
        meta.setAttribute('content', content)
        document.head.appendChild(meta)
      }

      removeOldMeta('description')
      removeOldMeta('og:title', 'property')
      removeOldMeta('og:description', 'property')
      removeOldMeta('og:type', 'property')
      removeOldMeta('og:image', 'property')

      setMeta('description', seo.descriptionHead)
      setMeta('og:title', seo.titleHead, 'property')
      setMeta('og:description', seo.descriptionHead, 'property')
      setMeta('og:type', seo.og_type || 'website', 'property')

      if (Array.isArray(seo.og_image)) {
        seo.og_image.forEach(img => setMeta('og:image', img, 'property'))
      }
    },
    handlePageChange(newPage) {
      if (this.currentPage === newPage) return
      const currentPath = this.$route.path
      // update URL only — watcher sẽ gọi ListMovie
      this.$router.push({
        path: currentPath,
        query: { ...this.$route.query, page: newPage },
      })
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  },
  watch: {
    // watch cả route để bắt được thay đổi path (params) hoặc query.page
    '$route': {
      handler(to) {
        const newPage = parseInt(to.query.page, 10) || 1
        const pathParam = Array.isArray(to.params.path)
          ? to.params.path.join('/')
          : (to.params.path || this.path)

        if (pathParam !== this.lastPath || newPage !== this.currentPage) {
          this.currentPage = newPage
          this.lastPath = pathParam
          this.ListMovie(pathParam)
        }
      },
      deep: true,
      immediate: true,
    },
  },
}
</script>

